import React, { Component } from 'react';
import { Drilldown, DrilldownButton, DrilldownLink } from 'react-drilldown';
import { keyBy, mapValues } from 'lodash/fp';
// import styles from './style.m.less';
import './style.css';


export default class StatefulExample extends Component {
    constructor() {
        super();
        this.state = {
            staggered: true,
            selected: 'gender',
        };

        this.sampleData = keyBy('name', [
            {
                name: 'gender',
                title: 'Choose a gender',
                parent: null,
                children: ['male', 'female'],
            },
            {
                name: 'male',
                title: 'Male Occasions',
                parent: 'gender',
                children: ['maleCasual', 'maleFormal', 'maleOutdoor'],
                component: () => <DrilldownButton onClick={() => this.onSelect('male')}>Male</DrilldownButton>,
            },
            {
                name: 'female',
                title: 'Female Occasions',
                parent: 'gender',
                children: ['femaleCasual', 'femaleFormal', 'femaleOutdoor'],
                component: () => <DrilldownButton onClick={() => this.onSelect('female')}>Female</DrilldownButton>,
            },
            {
                name: 'maleCasual',
                title: 'Male Casual Wear',
                parent: 'male',
                children: ['maleCasualShirts', 'maleCasualPants', 'maleCasualHats'],
                component: () => (
                    <DrilldownButton onClick={() => this.onSelect('maleCasual')}>Male Casual</DrilldownButton>
                ),
            },
            {
                name: 'maleCasualShirts',
                title: null,
                parent: 'maleCasual',
                children: [],
                component: () => <DrilldownLink href="#">Shirts</DrilldownLink>,
            },
            {
                name: 'maleCasualPants',
                title: null,
                parent: 'maleCasual',
                children: [],
                component: () => <DrilldownLink href="#">Pants</DrilldownLink>,
            },
            {
                name: 'maleCasualHats',
                title: null,
                parent: 'maleCasual',
                children: [],
                component: () => <DrilldownLink href="#">Hats</DrilldownLink>,
            },
            {
                name: 'maleFormal',
                title: 'Male Formal Wear',
                parent: 'male',
                children: ['maleFormalSuits', 'maleFormalShoes', 'maleFormalTies'],
                component: () => (
                    <DrilldownButton onClick={() => this.onSelect('maleFormal')}>Male Formal</DrilldownButton>
                ),
            },
            {
                name: 'maleFormalSuits',
                title: null,
                parent: 'maleFormal',
                children: [],
                component: () => <DrilldownLink href="#">Suits</DrilldownLink>,
            },
            {
                name: 'maleFormalShoes',
                title: null,
                parent: 'maleFormal',
                children: [],
                component: () => <DrilldownLink href="#">Shoes</DrilldownLink>,
            },
            {
                name: 'maleFormalTies',
                title: null,
                parent: 'maleFormal',
                children: [],
                component: () => <DrilldownLink href="#">Ties</DrilldownLink>,
            },
            {
                name: 'maleOutdoor',
                title: 'Male Outdoor Wear',
                parent: 'male',
                children: ['maleOutdoorBoots'],
                component: () => (
                    <DrilldownButton onClick={() => this.onSelect('maleOutdoor')}>Male Outdoor</DrilldownButton>
                ),
            },
            {
                name: 'maleOutdoorBoots',
                title: null,
                parent: 'maleOutdoor',
                children: [],
                component: () => <DrilldownLink href="#">Boots</DrilldownLink>,
            },
            {
                name: 'femaleCasual',
                title: 'Female Casual Wear',
                parent: 'female',
                children: ['femaleCasualSkirts', 'femaleCasualDresses', 'femaleCasualBags'],
                component: () => (
                    <DrilldownButton onClick={() => this.onSelect('femaleCasual')}>Female Casual</DrilldownButton>
                ),
            },
            {
                name: 'femaleCasualSkirts',
                title: null,
                parent: 'femaleCasual',
                children: [],
                component: () => <DrilldownLink href="#">Skirts</DrilldownLink>,
            },
            {
                name: 'femaleCasualDresses',
                title: null,
                parent: 'femaleCasual',
                children: [],
                component: () => <DrilldownLink href="#">Dresses</DrilldownLink>,
            },
            {
                name: 'femaleCasualBags',
                title: null,
                parent: 'femaleCasual',
                children: [],
                component: () => <DrilldownLink href="#">Bags</DrilldownLink>,
            },
            {
                name: 'femaleFormal',
                title: 'Female Formal Wear',
                parent: 'female',
                children: ['femaleFormalDresses', 'femaleFormalShoes'],
                component: () => (
                    <DrilldownButton onClick={() => this.onSelect('femaleFormal')}>Feale Formal</DrilldownButton>
                ),
            },
            {
                name: 'femaleFormalDresses',
                title: null,
                parent: 'femaleFormal',
                children: [],
                component: () => <DrilldownLink href="#">Dresses</DrilldownLink>,
            },
            {
                name: 'femaleFormalShoes',
                title: null,
                parent: 'femaleFormal',
                children: [],
                component: () => <DrilldownLink href="#">Shoes</DrilldownLink>,
            },
            {
                name: 'femaleOutdoor',
                title: 'Female Outdoor Wear',
                parent: 'female',
                children: ['femaleOutdoorBoots', 'femaleOutdoorClothing'],
                component: () => (
                    <DrilldownButton onClick={() => this.onSelect('femaleOutdoor')}>Female Outdoor</DrilldownButton>
                ),
            },
            {
                name: 'femaleOutdoorBoots',
                title: null,
                parent: 'femaleOutdoor',
                children: [],
                component: () => <DrilldownLink href="#">Boots</DrilldownLink>,
            },
            {
                name: 'femaleOutdoorClothing',
                title: null,
                parent: 'femaleOutdoor',
                children: [],
                component: () => <DrilldownLink href="#">Clothing</DrilldownLink>,
            },
        ]);

        this.items = mapValues(item => {
            return {
                ...item,
                children: item.children.map(name => this.sampleData[name].component),
            };
        }, this.sampleData);
    }

    toggleStaggered = () => {
        const { staggered } = this.state;
        this.setState({ staggered: !staggered });
    };

    onSelect = name => this.setState({ selected: name });

    render() {
        const { selected, staggered } = this.state;
        const itemComponents = this.items[selected].children;
        const currentTitle = this.items[selected].title;
        const parent = this.items[selected].parent;
        return (
            <div>
                <p>
                    <label>
                        <input type="checkbox" checked={this.state.staggered} onChange={this.toggleStaggered} />
                        Staggered Animation
                    </label>
                </p>
                <div className="Example-container">
                    <Drilldown
                        header={currentTitle}
                        footer={`${itemComponents.length} items`}
                        items={itemComponents}
                        staggeredAnimation={staggered}
                        onBackClick={parent ? () => this.onSelect(parent) : null}
                    />
                </div>
            </div>
        );
    }
}
